import logo from './logo.svg';
import './App.css';
import { LoginForm } from './components/LoginForm';
import { AddPost } from './components/post/AddPost';
import { useContext } from 'react';
import { AuthenContext } from './context/AuthenContext';
import {
  BrowserRouter,
  Routes,
  Route,
  Navigate,
  Outlet,
  Link,
} from "react-router-dom";
import { SearchCategory } from './components/SearchCategory';
import { AddCategory } from './components/AddCategory';
import { EditCategory } from './components/EditCategory';
import { useDispatch, useSelector } from 'react-redux';
import { decrement, increment, incrementAsync, incrementByAmount } from './redux/counterSlice ';

export const Footer = () => (<h1>Footer</h1>)

const name = "DEMO"

export function Hello() {
  const save = (s) => {
    alert(`Hello ${s}`)
  }
  return (
    <>
      <div>
        <h1> {name} </h1>
      </div>
      <div>
        <h1>Hello Js</h1>
        <form>
          <input placeholder='Name' />
          <button onClick={() => save("React")}>Save</button>
        </form>
      </div>
    </>
  )
}

export function MainApp() {
  return <BrowserRouter>
    <Routes>
      <Route path="/login" element={<LoginForm />} />

      <Route path="/dashboard" element={<App />}>
        <Route index element={<Navigate
          to={"/dashboard/categories"} />} />
        <Route path="category/add" element={<AddCategory />} />
        <Route path="category/:id/edit" element={<EditCategory />} />
        <Route path="categories" element={<SearchCategory />} />
      </Route>

      <Route path="/" element={<Navigate to={"/dashboard"} />} />
      <Route path="*" element={<div>404 Not Found.....</div>} />
    </Routes>
  </BrowserRouter >
}

// component
function App() {
  let { isAuthenticated, isLoading, logout } = useContext(AuthenContext);

  const { value } = useSelector((state) => state.counter)
  const dispatch = useDispatch()

  if (isLoading)
    return <div>Loading.....</div>

  if (isAuthenticated)
    return (
      <>
        <div className="row">
          <button onClick={logout}>Logout</button>
        </div>
        <div className="row">
          <div className='col-12'>
            <p>Count: {value}</p>
            <button onClick={() => dispatch(increment())}>increment</button>
            <button onClick={() => dispatch(incrementByAmount(5))}>decrement</button>
            <button onClick={() => dispatch(incrementAsync(5))}>decrement</button>
          </div>
        </div>

        <div className="row">
          <nav className='col-md-3'>
            <Link to="/dashboard/category/add">Add Category</Link>
            <Link to="/dashboard/categories">Categories</Link>
          </nav>
          <div className='col-md-9'>
            {/* outlet se duoc thay the boi element cua nested route */}
            <Outlet />
          </div>
        </div>
        <Footer />
      </>
    );
  // JSX
  return <Navigate to={"/login"} />;
}

export default App;
