import axios from 'axios';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';

const imageLogo = { width: 62 }

export function AddCategory() {
    let navigate = useNavigate()

    //ham hook
    let [category, setCategory] = useState({
        name: ""
    });

    const addNew = async () => {
        var config = {
            method: 'post',
            url: 'http://54.150.115.104:8080/api/admin/category/add',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": "Bearer " + localStorage.getItem("accessToken")
            },
            data: category
        };

        try {
            let response = await axios(config)

            let result = response.data;
            console.log(result)
            navigate("/dashboard/categories")
        } catch (error) {
            console.log(error);
        }
    }

    const handleChangeName = (e) => setCategory({
        ...category,
        name: e.target.value
    })

    return (
        <div className='row border'>
            <h2 className="h1">Create Category</h2>
            <div className="mb-3">
                <label className="form-label">Name</label>
                <input className='form-control' onChange={handleChangeName} />
            </div >
            <div className="mb-3">
                <button className="btn btn-danger" type='button' onClick={addNew}>Create</button>
            </div>
        </div>
    )
}