import axios from 'axios';
import { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { axiosInstance } from '../service/myaxios';

export function EditCategory() {
    let { id } = useParams();
    console.log(id)

    let navigate = useNavigate()

    //ham hook
    let [category, setCategory] = useState({ id, name: '' })

    useEffect(() => {
        getById()
    }, [id])

    const getById = async () => {
        try {
            var config = {
                method: 'get',
                url: `/category/${id}`
            };

            let response = await axiosInstance(config)
            let result = response.data;// ko co gi
            setCategory(result)
        } catch (error) {
            console.log(error);
        }
    }

    const save = async () => {
        var config = {
            method: 'put',
            url: '/admin/category/update',
            data: category
        };

        try {
            let response = await axiosInstance(config)

            let result = response.data;// ko co gi
            console.log(result)
            navigate("/dashboard/categories")
        } catch (error) {
            console.log(error);
        }
    }

    const handleChange = (e) => setCategory({
        ...category,
        [e.target.name]: e.target.value
    })

    return (
        <div>
            <h2>Update Category</h2>
            <div>
                <label>Id</label>
                <input onChange={handleChange} name="id" value={category?.id} />
            </div>
            <div>
                <label>Name</label>
                <input onChange={handleChange} name="name" />
            </div>
            <div>
                <button type='button' onClick={save}>Update</button>
            </div>
        </div>
    )
}