import { axiosInstance } from "./myaxios";

export const deleteCategory = async (id) => {
    var config = {
        method: 'DELETE',
        url: `/admin/category/delete?id=${id}`
    };

    try {
        await axiosInstance(config)
        return { code: 200 }
    } catch (error) {
        console.log(error);

        if (error.response) {
            return { code: error.response.status }
        } else if (error.request) {
            return { code: 408 }
        } else {
            return { code: 500 }
        }
    }
}

export const findCategories = async (search) => {
    var config = {
        method: 'post',
        url: '/category/search',
        data: search
    };
    try {
        let response = await axiosInstance(config)

        let result = response.data;// ko co gi
        return { code: 200, result }
    } catch (error) {
        console.log(error);

        if (error.response) {
            return { code: error.response.status }
        } else if (error.request) {
            return { code: 408 }
        } else {
            return { code: 500 }
        }
    }
}

export const addCategory = async (category) => {
    var config = {
        method: 'post',
        url: '/admin/category/add',
        data: category
    };

    try {
        let response = await axiosInstance(config)

        let result = response.data;
        return { code: 200, result }
    } catch (error) {
        console.log(error);

        if (error.response) {
            return { code: error.response.status }
        } else if (error.request) {
            return { code: 408 }
        } else {
            return { code: 500 }
        }
    }
}