import { useContext, useState } from "react";
import { AuthenContext } from "../context/AuthenContext";

export function useAuth() {
    let { isAuthenticated } = useContext(AuthenContext)

    let [online, setOnline] = useState(true)

    function check(a, b) {
        if (a > b)
            setOnline(false)
        else setOnline(true)
    }

    return { isAuthenticated, online, setOnline, check }
}