import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import { useError } from '../hooks/useError';
import { searchCategory, setCategorySearch } from '../redux/categorySlice';
import { deleteCategory, findCategories } from '../service/category.service';

export function SearchCategory() {
    // let { setCategory: setItem } = useContext(CategoryContext)

    let { isAuthenticated, online, check } = useAuth();
    const { checkError } = useError();

    // console.log(isAuthenticated)
    // console.log(online)
    // console.log(cateCtx)
    //ham hook
    // let [search, setSearch] = useState({
    //     start: 0,
    //     length: 10,
    //     keyword: ''
    // });

    // let [result, setResult] = useState({
    //     recordsTotal: 0,
    //     recordsFiltered: 0,
    //     data: []
    // })

    const result = useSelector((state) => state.category)
    const dispatch = useDispatch()
    let { search } = result

    useEffect(() => {
        const timeout = setTimeout(() => {
            //goi lai
            find()
        }, 500)
        return () => clearTimeout(timeout)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [search])

    const find = async () => {
        // let { code, result } = await findCategories(search)
        // if (code === 200) {
        //     setResult(result)
        // }
        dispatch(searchCategory())
    }

    const handleChange = (e) => dispatch(setCategorySearch({
        ...search,
        [e.target.name]: e.target.value
    }))

    const deleteItem = async (id) => {
        let { code } = await deleteCategory(id)

        if (code === 200) {
            find()
        } else {
            checkError(code)
        }
    }

    const back = () => {
        let start = search.start - parseInt(search.length)
        start = start < 0 ? 0 : start
        // setSearch({
        //     ...search,
        //     start
        // })
        dispatch(setCategorySearch({
            ...search,
            start
        }))
        check(1, 2)
    }

    const next = () => {
        let start = (search.start) + parseInt(search.length)
        console.log(start)
        if (start < result.recordsFiltered)
            // setSearch({
            //     ...search,
            //     start
            // })

            dispatch(setCategorySearch({
                ...search,
                start
            }))
        check(2, 1)
    }

    return (
        <div>
            <h1>{online.toString()} - {`${isAuthenticated}`}</h1>
            <h2>Search Category</h2>
            <div>
                <label>Keyword</label>
                <input onChange={handleChange} name="keyword" />

                <select onChange={handleChange} name="length"
                    value={search.length}>
                    <option value={5}>5</option>
                    <option value={10}>10</option>
                    <option value={15}>15</option>
                </select>

                <button type='button' onClick={back}>
                    <i className="fa-solid fa-backward"></i>
                </button>
                <button type='button' onClick={next}>
                    <i className="fa-solid fa-forward"></i>
                </button>
            </div>

            <table className="table table-striped">
                <thead>
                    <tr>
                        <th>Id</th>
                        <th>Name</th>
                        <th>Option</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        result.data.map(({ id, name }) => (
                            <tr key={id}>
                                <td>{id}</td>
                                <td>{name}</td>
                                <td>
                                    <button className='btn btn-danger' onClick={() => deleteItem(id)}>
                                        <i className="fa-solid fa-trash-can"></i> Delete
                                    </button>
                                    <Link className='btn btn-warning'
                                        to={`/dashboard/category/${id}/edit`}>
                                        <i className="fa-solid fa-pen-to-square"></i> Edit
                                    </Link>
                                </td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>
        </div>
    )
}